import React from 'react';

const UserInfo = ({
  title, linkedin, github, stackOverflow, gmail, aforisma, urlAforisma,
}) => (

  <div>
    {title && <h5 className="uppercase font-bold mb-2.5">{title}</h5>}

    <div className="flex justify-center mb-6">
      {linkedin && <LinkedIn href={linkedin} />}
      {github && <GitHub href={github} />}
      {stackOverflow && <Stackoverflow href={stackOverflow} />}
      {gmail && <Gmail href={gmail} />}
    </div>

    {aforisma && <Aforisma href={urlAforisma} text={aforisma} />}
  </div>

);

const Aforisma = ({ href, text }) => {
  const quote = <q>{text}</q>;

  return (href ? (
    <a className="r-underline" href={href} target="_black" type="button">
      {quote}
    </a>
  )
    : quote);
};

